/**
 * The common class of variables and constraints.
 *
 * @author Takuto Yanagida
 * @version 2022-08-15
 */

class Element {

	#index = -1;
	#name  = '';

	/**
	 * It is used when the user wishes to associate an arbitrary object with each element.
	 */
	userObject = null;

	/**
	 * Used when the solver wants to associate an arbitrary object with each element.
	 */
	solverObject = null;

	// Called only from Problem.
	setIndex(index) {
		this.#index = index;
	}

	/**
	 * Sets the name.
	 *
	 * @param name String representing the name.
	 */
	setName(name) {
		this.#name = name;
	}

	/**
	 * Get the index on the owned problem.
	 * Each variable and constraint is assigned a serial number as an index, which is used to access it through the problem.
	 *
	 * @return Integer value representing the index.
	 */
	index() {
		return this.#index;
	}

	/**
	 * Gets the name.
	 *
	 * @return String representing the name.
	 */
	name() {
		return this.#name;
	}

}


/**
 * An interface that represents the relationship between variables.
 * Use CrispRelation or FuzzyRelation class that implement this interface.
 *
 * @author Takuto Yanagida
 * @version 2023-03-25
 */

class Relation {

}

/**
 * The class represents crisp relationships between variables.
 *
 * @author Takuto Yanagida
 * @version 2023-03-25
 */

class CrispRelation extends Relation {

	/**
	 * Gets whether or not the relation is satisfied in this crisp relation.
	 * @param vals Values of each variable
	 * @return Whether or not it is satisfied.
	 */
	isSatisfied(...vals) {
		throw new Exception();
	}

	/**
	 * Returns a view as a fuzzy relation.
	 * @return A fuzzy relation.
	 */
	asFuzzyRelation() {
		return new FuzzyRelationView(this);
	}

}

/**
 * This class represents crisp relationships by table.
 *
 * @author Takuto YANAGIDA
 * @version 2023-03-26
 */

class CrispTabledRelation extends CrispRelation {

	#elms;
	#doms;
	#mul;

	constructor(elms, doms) {
		this.#elms = [...elms];
		this.#doms = [...doms];
		this.#mul  = new Array(doms.length);

		let m = 1;
		for (let i = this.#mul.length - 1; i >= 0; --i) {
			this.#mul[i] = m;
			m *= doms[i].size();
		}
	}

	/**
	 * Gets whether or not the relation is satisfied in this crisp relation.
	 * @param vals Values of each variable
	 * @return Whether or not it is satisfied.
	 */
	isSatisfied(...vals) {
		if (this.#mul.length !== vals.length) {
			throw new Exception();
		}
		let index = 0;
		for (let i = 0; i < this.#mul.length; ++i) {
			index += this.#mul[i] * this.#doms[i].indexOf(vals[i]);
		}
		return this.#elms[index];
	}

}

/**
 * Crisp relations defined by functions.
 *
 * @author Takuto Yanagida
 * @version 2023-04-04
 */

class CrispRelationFunction extends CrispRelation {

	#fn;

	constructor(fn) {
		super();
		this.#fn = fn;
	}

	/**
	 * Gets whether or not the relation is satisfied in this crisp relation.
	 * @param vals Values of each variable
	 * @return Whether or not it is satisfied.
	 */
	isSatisfied(...vals) {
		return this.#fn(...vals);
	}

}

/**
 * The class represents fuzzy relationships between variables.
 *
 * @author Takuto Yanagida
 * @version 2023-03-25
 */

class FuzzyRelation extends Relation {

	/**
	 * Gets the satisfaction degree in this fuzzy relation.
	 * @param vals Values of each variable
	 * @return A satisfaction degree d (0 <= d <= 1).
	 */
	satisfactionDegree(...vals) {
		throw new Exception();
	}

	/**
	 * Returns a view as a crisp relation.
	 * @return A crisp relation.
	 */
	asCrispRelation() {
		return new CrispRelationView(this);
	}

}

/**
 * This class represents fuzzy relationships by table.
 *
 * @author Takuto YANAGIDA
 * @version 2023-03-26
 */

class FuzzyTabledRelation extends FuzzyRelation {

	#elms;
	#doms;
	#mul;

	constructor(elms, doms) {
		this.#elms = [...elms];
		this.#doms = [...doms];
		this.#mul  = new Array(doms.length);

		let m = 1;
		for (let i = this.#mul.length - 1; i >= 0; --i) {
			this.#mul[i] = m;
			m *= doms[i].size();
		}
	}

	/**
	 * Gets the satisfaction degree in this fuzzy relation.
	 * @param vals Values of each variable
	 * @return A satisfaction degree d (0 <= d <= 1).
	 */
	satisfactionDegree(...vals) {
		if (this.#mul.length !== vals.length) {
			throw new Exception();
		}
		let index = 0;
		for (let i = 0; i < this.#mul.length; ++i) {
			index += this.#mul[i] * this.#doms[i].indexOf(vals[i]);
		}
		return this.#elms[index];
	}

}

/**
 * Fuzzy relations defined by functions.
 *
 * @author Takuto Yanagida
 * @version 2023-04-04
 */

class FuzzyRelationFunction extends FuzzyRelation {

	#fn;

	constructor(fn) {
		super();
		this.#fn = fn;
	}

	/**
	 * Gets the satisfaction degree in this fuzzy relation.
	 * @param vals Values of each variable
	 * @return A satisfaction degree d (0 <= d <= 1).
	 */
	satisfactionDegree(...vals) {
		return this.#fn(...vals);
	}

}

/**
 * View of relations.
 *
 * @author Takuto Yanagida
 * @version 2023-03-25
 */

class CrispRelationView extends CrispRelation {

	constructor(that) {
		this.that = that;
	}

	isSatisfied(...vals) {
		return this.that.satisfactionDegree(vals) === 1;
	}

}

class FuzzyRelationView extends FuzzyRelation {

	constructor(that) {
		this.that = that;
	}

	satisfactionDegree(...vals) {
		return this.that.isSatisfied(vals) ? 1 : 0;
	}

}


/**
 * The class represents a constraint.
 *
 * @author Takuto Yanagida
 * @version 2023-03-29
 */

class Constraint extends Element {

	/**
	 * The constant indicating that the satisfaction degree is not defined.
	 */
	static UNDEFINED = -1;

	rel;

	// Called only from Problem.
	constructor(r) {
		super();
		this.rel = r;
	}

	/**
	 * Returns the crisp relation between variables.
	 * @return Relation.
	 */
	crispRelation() {
		return this.rel;
	}

	/**
	 * Returns the fuzzy relation between variables.
	 * @return Relation.
	 */
	fuzzyRelation() {
		return this.rel;
	}

	/**
	 * Returns whether this is a fuzzy constraint.
	 * @return True if it is fuzzy constraint.
	 */
	isFuzzy() {
		return this.rel instanceof FuzzyRelation;
	}

	/**
	 * Returns a string representation.
	 * @return A string representation.
	 */
	toString() {
		const s = this.satisfactionDegree();
		return `c${this.index()}${this.name() === '' ? '' : `(${this.name()})`} = ${s === Constraint.UNDEFINED ? 'UNDEFINED' : s}`;
	}

	/**
	 * Returns the order of the constraint, i.e., the number of (associated) variables in the scope.
	 * @return Order.
	 */
	size() {}

	/**
	 * Gets the associated variable by specifying its index.
	 * @param index Index.
	 * @return A variable.
	 */
	at(index) {}

	/**
	 * Returns whether the specified variable is associated or not.
	 * @param v A variable.
	 * @return True if it is associated.
	 */
	constrains(v) {}

	/**
	 * Gets the index of a specified variable.
	 * If not found, returns -1.
	 * @param v A variable.
	 * @return Index.
	 */
	indexOf(v) {}

	/**
	 * Returns the number of scope variables that have not been assigned a value.
	 * @return Number of variables
	 */
	emptyVariableSize() {}

	/**
	 * Returns whether or not the satisfaction (degree) is defined.
	 * Satisfaction (degree) is defined when all associated variables have values assigned to them.
	 * @return True if it is defined.
	 */
	isDefined() {}

	/**
	 * Returns whether or not this constraint is satisfied.
	 * @return 1 if satisfied, 0 if not, UNDEFINED if undefined
	 */
	isSatisfied() {}

	/**
	 * Gets the current satisfaction degree.
	 * @return Degree 0.0 - 1.0, UNDEFINED if undefined.
	 */
	satisfactionDegree() {}

	/**
	 * Returns the set of constraints connected via the associated variables.
	 * @return A set of constraints.
	 */
	neighbors(dest) {}

	/**
	 * Calculates the highest consistency degree.
	 * That is, it seeks the highest satisfaction degree of the possible combinations of variable assignments for a given constraint.
	 * When all associated variables have been assigned values, it returns the same value as getSatisfactionDegree().
	 * @return The highest consistency degree.
	 */
	highestConsistencyDegree() {}

	/**
	 * Calculates the lowest consistency degree.
	 * That is, it seeks the lowest satisfaction degree of the possible combinations of variable assignments for a given constraint.
	 * When all associated variables have been assigned values, it returns the same value as getSatisfactionDegree().
	 * @return The lowest consistency degree.
	 */
	lowestConsistencyDegree() {}

}

/**
 * The class represents an unary constraint.
 * The constructor is not called directly, since it is created by the Problem.
 *
 * @author Takuto Yanagida
 * @version 2023-03-26
 */

class Constraint1 extends Constraint {

	#v;

	// Called only from Problem.
	constructor(r, v) {
		super(r);
		this.#v = v;
	}

	/**
	 * {@inheritDoc}
	 */
	size() {
		return 1;
	}

	/**
	 * {@inheritDoc}
	 */
	at(index) {
		if (index === 0) return this.#v;
		throw new IndexOutOfBoundsException();
	}

	/**
	 * {@inheritDoc}
	 */
	constrains(v) {
		return v === this.#v;
	}

	/**
	 * {@inheritDoc}
	 */
	indexOf(v) {
		return (v === this.#v) ? 0 : -1;
	}

	/**
	 * {@inheritDoc}
	 */
	emptyVariableSize() {
		return this.#v.isEmpty() ? 1 : 0;
	}

	/**
	 * {@inheritDoc}
	 */
	isDefined() {
		return !this.#v.isEmpty();
	}

	/**
	 * {@inheritDoc}
	 */
	isSatisfied() {
		if (this.#v.isEmpty()) return Constraint.UNDEFINED;
		return this.crispRelation().isSatisfied(this.#v.value()) ? 1 : 0;
	}

	/**
	 * {@inheritDoc}
	 */
	satisfactionDegree() {
		if (this.#v.isEmpty()) return Constraint.UNDEFINED;
		return this.fuzzyRelation().satisfactionDegree(this.#v.value());
	}

	/**
	 * {@inheritDoc}
	 */
	neighbors(dest) {
		for (let i = 0, n = this.#v.size(); i < n; ++i) {
			const c = this.#v.at(i);
			if (c !== this) dest.push(c);
		}
		return dest;
	}

	/**
	 * {@inheritDoc}
	 */
	highestConsistencyDegree() {
		const sd = this.satisfactionDegree();
		if (sd !== Constraint.UNDEFINED) return sd;
		let cd = 0.0;
		const d = this.#v.domain();

		for (let i = 0, n = d.size(); i < n; ++i) {
			const s = this.fuzzyRelation().satisfactionDegree(d.at(i));
			if (s > cd) cd = s;
			if (cd === 1.0) break;
		}
		return cd;
	}

	/**
	 * {@inheritDoc}
	 */
	lowestConsistencyDegree() {
		const sd = this.satisfactionDegree();
		if (sd !== Constraint.UNDEFINED) return sd;
		let cd = 1.0;
		const d = this.#v.domain();

		for (let i = 0, n = d.size(); i < n; ++i) {
			const s = this.fuzzyRelation().satisfactionDegree(d.at(i));
			if (s < cd) cd = s;
			if (cd === 0.0) break;
		}
		return cd;
	}

}

/**
 * The class represents an binary constraint.
 * The constructor is not called directly, since it is created by the Problem.
 *
 * @author Takuto Yanagida
 * @version 2023-03-26
 */

class Constraint2 extends Constraint {

	#v1;
	#v2;

	// Called only from Problem.
	constructor(r, v1, v2) {
		super(r);
		this.#v1 = v1;
		this.#v2 = v2;
	}

	/**
	 * {@inheritDoc}
	 */
	size() {
		return 2;
	}

	/**
	 * {@inheritDoc}
	 */
	at(index) {
		if (index === 0) return this.#v1;
		if (index === 1) return this.#v2;
		throw new IndexOutOfBoundsException();
	}

	/**
	 * {@inheritDoc}
	 */
	constrains(v) {
		return this.#v1 === v || this.#v2 === v;
	}

	/**
	 * {@inheritDoc}
	 */
	indexOf(v) {
		if (v === this.#v1) return 0;
		if (v === this.#v2) return 1;
		return -1;
	}

	/**
	 * {@inheritDoc}
	 */
	emptyVariableSize() {
		let sum = 0;
		if (this.#v1.isEmpty()) ++sum;
		if (this.#v2.isEmpty()) ++sum;
		return sum;
	}

	/**
	 * {@inheritDoc}
	 */
	isDefined() {
		return !this.#v1.isEmpty() && !this.#v2.isEmpty();
	}

	/**
	 * {@inheritDoc}
	 */
	isSatisfied() {
		if (this.#v1.isEmpty() || this.#v2.isEmpty()) return Constraint.UNDEFINED;
		return this.crispRelation().isSatisfied(this.#v1.value(), this.#v2.value()) ? 1 : 0;
	}

	/**
	 * {@inheritDoc}
	 */
	satisfactionDegree() {
		if (this.#v1.isEmpty() || this.#v2.isEmpty()) return Constraint.UNDEFINED;
		return this.fuzzyRelation().satisfactionDegree(this.#v1.value(), this.#v2.value());
	}

	/**
	 * {@inheritDoc}
	 */
	neighbors(dest) {
		for (let i = 0, n = this.#v1.size(); i < n; ++i) {
			const c = this.#v1.at(i);
			if (c !== this) dest.push(c);
		}
		for (let i = 0, n = this.#v2.size(); i < n; ++i) {
			const c = this.#v2.at(i);
			if (c !== this) dest.push(c);
		}
		return dest;
	}

	/**
	 * {@inheritDoc}
	 */
	highestConsistencyDegree() {
		const sd = this.satisfactionDegree();
		if (sd !== Constraint.UNDEFINED) return sd;
		let cd = 0.0;
		const val1 = this.#v1.value();
		const val2 = this.#v2.value();
		const d1   = this.#v1.domain();
		const d2   = this.#v2.domain();
		const n1   = d1.size();
		const n2   = d2.size();

		if (!this.#v1.isEmpty() && this.#v2.isEmpty()) {
			for (let i = 0; i < n2; ++i) {
				const s = this.fuzzyRelation().satisfactionDegree(val1, d2.at(i));
				if (s > cd) cd = s;
				if (cd === 1.0) break;
			}
		} else if(this.#v1.isEmpty() && !this.#v2.isEmpty()) {
			for (let i = 0; i < n1; ++i) {
				const s = this.fuzzyRelation().satisfactionDegree(d1.at(i), val2);
				if (s > cd) cd = s;
				if (cd === 1.0) break;
			}
		} else {
			for (let i = 0; i < n1; ++i) {
				for (let j = 0; j < n2; ++j) {
					const s = this.fuzzyRelation().satisfactionDegree(d1.at(i), d2.at(j));
					if (s > cd) cd = s;
					if (cd === 1.0) break;
				}
			}
		}
		return cd;
	}

	/**
	 * {@inheritDoc}
	 */
	lowestConsistencyDegree() {
		const sd = this.satisfactionDegree();
		if(sd !== Constraint.UNDEFINED) return sd;
		let cd = 1.0;
		const val1 = this.#v1.value();
		const val2 = this.#v2.value();
		const d1   = this.#v1.domain();
		const d2   = this.#v2.domain();
		const n1   = d1.size();
		const n2   = d2.size();

		if (!this.#v1.isEmpty() && this.#v2.isEmpty()) {
			for (let i = 0; i < n2; ++i) {
				const s = this.fuzzyRelation().satisfactionDegree(val1, d2.at(i));
				if (s < cd) cd = s;
				if (cd === 0.0) break;
			}
		} else if (this.#v1.isEmpty() && !this.#v2.isEmpty()) {
			for (let i = 0; i < n1; ++i) {
				const s = this.fuzzyRelation().satisfactionDegree(d1.at(i), val2);
				if (s < cd) cd = s;
				if (cd === 0.0) break;
			}
		} else {
			for (let i = 0; i < n1; ++i) {
				for (let j = 0; j < n2; ++j) {
					const s = this.fuzzyRelation().satisfactionDegree(d1.at(i), d2.at(j));
					if (s < cd) cd = s;
					if (cd === 0.0) break;
				}
			}
		}
		return cd;
	}

}

/**
 * The class represents an 3-ary constraint.
 * The constructor is not called directly, since it is created by the Problem.
 *
 * @author Takuto Yanagida
 * @version 2023-03-26
 */

class Constraint3 extends Constraint {

	#v1;
	#v2;
	#v3;

	// Called only from Problem.
	constructor(r, v1, v2, v3) {
		super(r);
		this.#v1 = v1;
		this.#v2 = v2;
		this.#v3 = v3;
	}

	/**
	 * {@inheritDoc}
	 */
	size() {
		return 3;
	}

	/**
	 * {@inheritDoc}
	 */
	at(index) {
		if (index === 0) return this.#v1;
		if (index === 1) return this.#v2;
		if (index === 2) return this.#v3;
		throw new IndexOutOfBoundsException();
	}

	/**
	 * {@inheritDoc}
	 */
	constrains(v) {
		return this.#v1 === v || this.#v2 === v || this.#v3 === v;
	}

	/**
	 * {@inheritDoc}
	 */
	indexOf(v) {
		if (v === this.#v1) return 0;
		if (v === this.#v2) return 1;
		if (v === this.#v3) return 2;
		return -1;
	}

	/**
	 * {@inheritDoc}
	 */
	emptyVariableSize() {
		let sum = 0;
		if (this.#v1.isEmpty()) ++sum;
		if (this.#v2.isEmpty()) ++sum;
		if (this.#v3.isEmpty()) ++sum;
		return sum;
	}

	/**
	 * {@inheritDoc}
	 */
	isDefined() {
		return !this.#v1.isEmpty() && !this.#v2.isEmpty() && !this.#v3.isEmpty();
	}

	/**
	 * {@inheritDoc}
	 */
	isSatisfied() {
		if (this.#v1.isEmpty() || this.#v2.isEmpty() || this.#v3.isEmpty()) return -1;
		return this.crispRelation().isSatisfied(this.#v1.value(), this.#v2.value(), this.#v3.value()) ? 1 : 0;
	}

	/**
	 * {@inheritDoc}
	 */
	satisfactionDegree() {
		if (this.#v1.isEmpty() || this.#v2.isEmpty() || this.#v3.isEmpty()) return Constraint.UNDEFINED;
		return this.fuzzyRelation().satisfactionDegree(this.#v1.value(), this.#v2.value(), this.#v3.value());
	}

	/**
	 * {@inheritDoc}
	 */
	neighbors(dest) {
		for (let i = 0, n = this.#v1.size(); i < n; ++i) {
			const c = this.#v1.at(i);
			if (c !== this) dest.push(c);
		}
		for (let i = 0, n = this.#v2.size(); i < n; ++i) {
			const c = this.#v2.at(i);
			if (c !== this) dest.push(c);
		}
		for (let i = 0, n = this.#v3.size(); i < n; ++i) {
			const c = this.#v3.at(i);
			if (c !== this) dest.push(c);
		}
		return dest;
	}

	/**
	 * {@inheritDoc}
	 */
	highestConsistencyDegree() {
		const sd = this.satisfactionDegree();
		if (sd !== Constraint.UNDEFINED) return sd;
		let cd = 1.0;
		const val1 = this.#v1.value();
		const val2 = this.#v2.value();
		const val3 = this.#v3.value();
		const d1   = this.#v1.domain();
		const d2   = this.#v2.domain();
		const d3   = this.#v3.domain();
		const n1   = d1.size();
		const n2   = d2.size();
		const n3   = d3.size();

		if (this.#v1.isEmpty() && !this.#v2.isEmpty() && !this.#v3.isEmpty()) {
			for (let i = 0; i < n1; ++i) {
				const s = this.fuzzyRelation().satisfactionDegree(d1.at(i), val2, val3);
				if (s > cd) cd = s;
				if (cd === 1.0) break;
			}
		} else if (!this.#v1.isEmpty() && this.#v2.isEmpty() && !this.#v3.isEmpty()) {
			for (let i = 0; i < n2; ++i) {
				const s = this.fuzzyRelation().satisfactionDegree(val1, d2.at(i), val3);
				if (s > cd) cd = s;
				if (cd === 1.0) break;
			}
		} else if (!this.#v1.isEmpty() && !this.#v2.isEmpty() && this.#v3.isEmpty()) {
			for (let i = 0; i < n3; ++i) {
				const s = this.fuzzyRelation().satisfactionDegree(val1, val2, d3.at(i));
				if (s > cd) cd = s;
				if (cd === 1.0) break;
			}

		} else if (this.#v1.isEmpty() && this.#v2.isEmpty() && !this.#v3.isEmpty()) {
			for (let i = 0; i < n1; ++i) {
				for (let j = 0; j < n2; ++j) {
					const s = this.fuzzyRelation().satisfactionDegree(d1.at(i), d2.at(j), val3);
					if (s > cd) cd = s;
					if (cd === 1.0) break;
				}
			}
		} else if (this.#v1.isEmpty() && !this.#v2.isEmpty() && this.#v3.isEmpty()) {
			for (let i = 0; i < n1; ++i) {
				for (let j = 0; j < n3; ++j) {
					const s = this.fuzzyRelation().satisfactionDegree(d1.at(i), val2, d3.at(j));
					if (s > cd) cd = s;
					if (cd === 1.0) break;
				}
			}
		} else if (!this.#v1.isEmpty() && this.#v2.isEmpty() && this.#v3.isEmpty()) {
			for (let i = 0; i < n2; ++i) {
				for (let j = 0; j < n3; ++j) {
					const s = this.fuzzyRelation().satisfactionDegree(val1, d2.at(i), d3.at(j));
					if (s > cd) cd = s;
					if (cd === 1.0) break;
				}
			}
		} else {
			for (let i = 0; i < n1; ++i) {
				for (let j = 0; j < n2; ++j) {
					for(let k = 0; k < n3; ++k) {
						const s = this.fuzzyRelation().satisfactionDegree(d1.at(i), d2.at(j), d3.at(k));
						if (s > cd) cd = s;
						if (cd === 1.0) break;
					}
				}
			}
		}
		return cd;
	}

	/**
	 * {@inheritDoc}
	 */
	lowestConsistencyDegree() {
		const sd = this.satisfactionDegree();
		if (sd !== Constraint.UNDEFINED) return sd;
		let cd = 1.0;
		const val1 = this.#v1.value();
		const val2 = this.#v2.value();
		const val3 = this.#v3.value();
		const d1   = this.#v1.domain();
		const d2   = this.#v2.domain();
		const d3   = this.#v3.domain();
		const n1   = d1.size();
		const n2   = d2.size();
		const n3   = d3.size();

		if (this.#v1.isEmpty() && !this.#v2.isEmpty() && !this.#v3.isEmpty()) {
			for (let i = 0; i < n1; ++i) {
				const s = this.fuzzyRelation().satisfactionDegree(d1.at(i), val2, val3);
				if (s < cd) cd = s;
				if (cd === 0.0) break;
			}
		} else if (!this.#v1.isEmpty() && this.#v2.isEmpty() && !this.#v3.isEmpty()) {
			for (let i = 0; i < n2; ++i) {
				const s = this.fuzzyRelation().satisfactionDegree(val1, d2.at(i), val3);
				if (s < cd) cd = s;
				if (cd === 0.0) break;
			}
		} else if (!this.#v1.isEmpty() && !this.#v2.isEmpty() && this.#v3.isEmpty()) {
			for (let i = 0; i < n3; ++i) {
				const s = this.fuzzyRelation().satisfactionDegree(val1, val2, d3.at(i));
				if (s < cd) cd = s;
				if (cd === 0.0) break;
			}
		} else if (this.#v1.isEmpty() && this.#v2.isEmpty() && !this.#v3.isEmpty()) {
			for (let i = 0; i < n1; ++i) {
				for (let j = 0; j < n2; ++j) {
					const s = this.fuzzyRelation().satisfactionDegree(d1.at(i), d2.at(j), val3);
					if (s < cd) cd = s;
					if (cd === 0.0) break;
				}
			}
		} else if (this.#v1.isEmpty() && !this.#v2.isEmpty() && this.#v3.isEmpty()) {
			for (let i = 0; i < n1; ++i) {
				for (let j = 0; j < n3; ++j) {
					const s = this.fuzzyRelation().satisfactionDegree(d1.at(i), val2, d3.at(j));
					if (s < cd) cd = s;
					if (cd === 0.0) break;
				}
			}
		} else if (!this.#v1.isEmpty() && this.#v2.isEmpty() && this.#v3.isEmpty()) {
			for (let i = 0; i < n2; ++i) {
				for (let j = 0; j < n3; ++j) {
					const s = this.fuzzyRelation().satisfactionDegree(val1, d2.at(i), d3.at(j));
					if (s < cd) cd = s;
					if (cd === 0.0) break;
				}
			}
		} else {
			for (let i = 0; i < n1; ++i) {
				for (let j = 0; j < n2; ++j) {
					for(let k = 0; k < n3; ++k) {
						const s = this.fuzzyRelation().satisfactionDegree(d1.at(i), d2.at(j), d3.at(k));
						if (s < cd) cd = s;
						if (cd === 0.0) break;
					}
				}
			}
		}
		return cd;
	}

}

/**
 * The class represents an n-ary constraint.
 * The constructor is not called directly, since it is created by the Problem.
 *
 * @author Takuto Yanagida
 * @version 2023-03-26
 */

class ConstraintN extends Constraint {

	#vars;
	#vals;  // For reuse.

	// Called only from Problem.
	constructor(r, ...vs) {
		super(r);
		this.#vars = [...vs];
		this.#vals = new Array(this.#vars.length);
	}

	/**
	 * {@inheritDoc}
	 */
	size() {
		return this.#vars.length;
	}

	/**
	 * {@inheritDoc}
	 */
	at(index) {
		return this.#vars[index];
	}

	/**
	 * {@inheritDoc}
	 */
	constrains(v) {
		return this.#vars.includes(v);
	}

	/**
	 * {@inheritDoc}
	 */
	indexOf(v) {
		return this.#vars.indexOf(v);
	}

	/**
	 * {@inheritDoc}
	 */
	emptyVariableSize() {
		let sum = 0;
		for (const v of this.#vars) {
			if (v.isEmpty()) ++sum;
		}
		return sum;
	}

	/**
	 * {@inheritDoc}
	 */
	isDefined() {
		for (const v of this.#vars) {
			if (v.isEmpty()) return false;
		}
		return true;
	}

	/**
	 * {@inheritDoc}
	 */
	isSatisfied() {
		for (let i = 0; i < this.#vars.length; ++i) {
			if (this.#vars[i].isEmpty()) return -1;
			this.#vals[i] = this.#vars[i].value();
		}
		return this.crispRelation().isSatisfied(...this.#vals) ? 1 : 0;
	}

	/**
	 * {@inheritDoc}
	 */
	satisfactionDegree() {
		for (let i = 0; i < this.#vars.length; ++i) {
			const v = this.#vars[i];
			if (v.isEmpty()) return Constraint.UNDEFINED;
			this.#vals[i] = v.value();
		}
		return this.fuzzyRelation().satisfactionDegree(...this.#vals);
	}

	/**
	 * {@inheritDoc}
	 */
	neighbors(dest) {
		dest.length = 0;
		for (const v of this.#vars) {
			for (let i = 0, I = v.size(); i < I; ++i) {
				const c = v.at(i);
				if (c !== this) dest.push(c);
			}
		}
		return dest;
	}

	/**
	 * {@inheritDoc}
	 */
	highestConsistencyDegree() {
		const sd = this.satisfactionDegree();
		if (sd !== Constraint.UNDEFINED) {
			return sd;
		}
		const emptyIndices = new Array(this.emptyVariableSize());
		let c = 0;

		for (let i = 0; i < this.#vars.length; ++i) {
			if (this.#vars[i].isEmpty()) {
				emptyIndices[c++] = i;
			} else {
				this.#vals[i] = this.#vars[i].value();
			}
		}
		return this.checkHCD(emptyIndices, 0, 0);
	}

	/**
	 * {@inheritDoc}
	 */
	lowestConsistencyDegree() {
		const sd = this.satisfactionDegree();
		if (sd !== Constraint.UNDEFINED) {
			return sd;
		}
		const emptyIndices = new Array(this.emptyVariableSize());
		let c = 0;

		for (let i = 0; i < this.#vars.length; ++i) {
			if (this.#vars[i].isEmpty()) {
				emptyIndices[c++] = i;
			} else {
				this.#vals[i] = this.#vars[i].value();
			}
		}
		return this.checkLCD(emptyIndices, 0, 1);
	}

	checkHCD(emptyIndices, currentStep, cd) {
		const index = emptyIndices[currentStep];
		const d     = this.#vars[index].domain();

		if (currentStep === emptyIndices.length - 1) {
			for (let i = 0, n = d.size(); i < n; ++i) {
				this.#vals[index] = d.at(i);
				const s = this.fuzzyRelation().satisfactionDegree(this.#vals);
				if (s > cd) cd = s;
				if (cd === 1.0) break;
			}
		} else {
			for (let i = 0, n = d.size(); i < n; ++i) {
				this.#vals[index] = d.at(i);
				cd = this.checkLCD(emptyIndices, currentStep + 1, cd);
			}
		}
		return cd;
	}

	checkLCD(emptyIndices, currentStep, cd) {
		const index = emptyIndices[currentStep];
		const d     = this.#vars[index].domain();

		if (currentStep === emptyIndices.length - 1) {
			for (let i = 0, n = d.size(); i < n; ++i) {
				this.#vals[index] = d.at(i);
				const s = this.fuzzyRelation().satisfactionDegree(this.#vals);
				if (s < cd) cd = s;
				if (cd === 0.0) break;
			}
		} else {
			for (let i = 0, n = d.size(); i < n; ++i) {
				this.#vals[index] = d.at(i);
				cd = this.checkLCD(emptyIndices, currentStep + 1, cd);
			}
		}
		return cd;
	}

}


/**
 * An abstract class that represents a variable domain. It also provides factory methods.
 * The domain is immutable.
 *
 * @author Takuto Yanagida
 * @version 2023-03-26
 */

class Domain {

	/**
	 * Returns whether the specified value is included as an element of the domain.
	 *
	 * @param val A value.
	 * @return True if the value included, false otherwise.
	 */
	contains(val) {}

	/**
	 * Returns the index of the specified value. If it does not exist, -1 is returned.
	 *
	 * @param val A value.
	 * @return The index.
	 */
	indexOf(val) {}

	/**
	 * Returns the size of the domain, including the pruned elements.
	 *
	 * @return The size.
	 */
	size() {}

	/**
	 * Returns the value at the specified index. The retrieved value may have been pruned.
	 *
	 * @param index An index.
	 * @return The value.
	 */
	at(index) {}

	/**
	 * Returns an arbitrary value, regardless of whether it has been pruned or not.
	 *
	 * @return A value.
	 */
	random() {}

}

/**
 * A variable domain with arbitrary integer elements.
 *
 * @author Takuto Yanagida
 * @version 2022-08-15
 */

class DomainArbitrary extends Domain {

	#vals;

	constructor(vals) {
		super();
		this.#vals = [...vals];
	}

	/**
	 * {@inheritDoc}
	 */
	contains(val) {
		return this.#vals.includes(val);
	}

	/**
	 * {@inheritDoc}
	 */
	indexOf(val) {
		return this.#vals.indexOf(val);
	}

	/**
	 * {@inheritDoc}
	 */
	size() {
		return this.#vals.length;
	}

	/**
	 * {@inheritDoc}
	 */
	at(index) {
		return this.#vals[index];
	}

	/**
	 * {@inheritDoc}
	 */
	random() {
		return this.#vals[Math.floor(Math.random() * this.#vals.length)];
	}

}

/**
 * A variable domain with contiguous integer elements.
 *
 * @author Takuto Yanagida
 * @version 2022-08-15
 */

class DomainRanged extends Domain {

	#min;
	#max;

	constructor(min, max) {
		super();
		this.#min = min;
		this.#max = max;
	}

	/**
	 * {@inheritDoc}
	 */
	contains(val) {
		return this.#min <= val && val <= this.#max;
	}

	/**
	 * {@inheritDoc}
	 */
	indexOf(val) {
		return (this.#min <= val && val <= this.#max) ? (val - this.#min) : -1;
	}

	/**
	 * {@inheritDoc}
	 */
	size() {
		return this.#max - this.#min + 1;
	}

	/**
	 * {@inheritDoc}
	 */
	at(index) {
		return this.#min + index;
	}

	/**
	 * {@inheritDoc}
	 */
	random() {
		return Math.floor(Math.random() * (this.#max - this.#min + 1) + this.#min);
	}

}


/**
 * Class that represents a variable.
 *
 * @author Takuto Yanagida
 * @version 2023-03-25
 */

class Variable extends Element {

	static #INVALID = Number.MIN_VALUE;

	#owner;
	#cons = [];
	#dom;
	#val  = Variable.#INVALID;

	// Called only from Problem.
	constructor(owner, d) {
		super();
		this.#owner = owner;
		this.#dom   = d;
	}

	// Called only from Problem.
	connect(c) {
		if (this.isConstrainedBy(c)) {
			throw new IllegalArgumentException();
		}
		this.#cons.push(c);
	}

	// Called only from Problem.
	deconnect(c) {
		if (!this.isConstrainedBy(c)) {
			throw new IllegalArgumentException();
		}
		this.#cons = this.#cons.filter(n => n !== c);
	}

	/**
	 * Assign a value.
	 * @param value Value.
	 */
	assign(value) {
		this.#val = value;  // Do not change val_ except here.
	}

	/**
	 * Sets the state of the variable to unassigned.
	 */
	clear() {
		this.assign(Variable.#INVALID);  // Do not use the invalid value except here and below (isEmpty).
	}

	/**
	 * Returns whether the value is unassigned or not.
	 * @return True if unassigned.
	 */
	isEmpty() {
		return this.value() === Variable.#INVALID;
	}

	/**
	 * Assign the domain.
	 * The variable will be in unassigned state.
	 * @param d Domain to be assigned.
	 */
	setDomain(d) {
		this.#dom = d;
		this.clear();
	}

	/**
	 * Returns the problem that owns this variable.
	 * @return Owner.
	 */
	owner() {
		return this.#owner;
	}

	/**
	 * Returns the number of associated constraints.
	 * @return Number of constraints.
	 */
	size() {
		return this.#cons.length;
	}

	/**
	 * Retrieves the associated constraints by specifying their indices.
	 * @param index Index.
	 * @return A constraint.
	 */
	at(index) {
		return this.#cons[index];
	}

	/**
	 * Returns an array containing all the constraints associated with the variable.
	 * If there are no constraints, returns an empty array.
	 * @return An array of constraints.
	 */
	constraints() {
		return [...this.#cons];
	}

	/**
	 * Returns the domain of the variable.
	 * @return The domain.
	 */
	domain() {
		return this.#dom;
	}

	/**
	 * Returns whether or not the variable is associated with the specified constraint.
	 * @param c A constraint.
	 * @return True if associated.
	 */
	isConstrainedBy(c) {
		return this.#cons.includes(c);
	}

	/**
	 * Returns a string representation.
	 * @return A string representation.
	 */
	toString() {
		return `x${this.index()}${this.name() === '' ? '' : `(${this.name()})`} = ${this.isEmpty() ? '<empty>' : this.value()}`;
	}

	/**
	 * Returns the value of the variable.
	 * @returnThe value of the variable.
	 */
	value() {
		return this.#val;
	}

	/**
	 * Returns the set of variables connected via the associated constraints.
	 * @return An array of variables
	 */
	neighbors() {
		const dest = [];
		for (const c of this.#cons) {
			for(let j = 0, m = c.size(); j < m; j += 1) {
				const v = c.at(j);
				if (v !== this) dest.push(v);
			}
		}
		return dest;
	}

}


/**
 * The class represents a constraint satisfaction problem.
 *
 * @author Takuto Yanagida
 * @version 2023-04-07
 */

class Problem {

	_isFuzzy = false;
	_vars    = [];
	_cons    = [];

	// Generation Methods --------

	/**
	 * Adds a variable to this problem.
	 * @param Variable v A variable.
	 */
	addVariable(v) {
		v.setIndex(this._vars.length);
		this._vars.push(v);
	}

	/**
	 * Generates a domain.
	 * @param args {
	 *   @type Array 'values' Multiple values.
	 *
	 *   @type Number 'min' Minimum value.
	 *   @type Number 'max' Maximum value.
	 * }
	 * @return A domain.
	 */
	createDomain(args) {
		if (args.values) {
			return new DomainArbitrary(args.values);
		} else if ('min' in args && 'max' in args) {
			return new DomainRanged(args.min, args.max);
		}
		return null;
	}

	/**
	 * Generates a variable.
	 * @param Array args {
	 *   @type string 'name'   Display name.
	 *   @type Domain 'domain' A domain.
	 *   @type *      'value'  A value.
	 * }
	 * @return A variable.
	 */
	createVariable(args) {
		if (args.value && !args.domain.contains(args.value)) {
			throw new Error();
		}
		const v = new Variable(this, args.domain);
		this.addVariable(v);
		if (args.name) v.setName(args.name);
		if (args.value) v.assign(args.value);
		return v;
	}

	/**
	 * Generates a constraint.
	 * @param Array args {
	 *   @type string   'name'      Display name.
	 *   @type Array    'variables' Variables.
	 *   @type Relation 'relation'  A relation.
	 * }
	 * @return A constraint.
	 */
	createConstraint(args) {
		for (const v of args.variables) {
			if (v.owner() !== this) return null;
		}
		let c;
		if (args.variables.length === 1)      c = new Constraint1(args.relation, ...args.variables);
		else if (args.variables.length === 2) c = new Constraint2(args.relation, ...args.variables);
		else if (args.variables.length === 3) c = new Constraint3(args.relation, ...args.variables);
		else c = new ConstraintN(args.relation, args.variables);
		c.setIndex(this._cons.length);
		this._cons.push(c);
		for (const v of args.variables) v.connect(c);
		if (c.isFuzzy()) this._isFuzzy = true;
		if (args.name) c.setName(args.name);
		return c;
	}

	//  Modification Methods --------

	/**
	 * Remove the constraint.
	 * @param c Constraints to be removed.
	 */
	removeConstraint(c) {
		const index = this._cons.indexOf(c);
		this._cons.remove(c);
		for (let i = index; i < this._cons.length; ++i) {
			this._cons[i].setIndex(i);
		}
		for (let i = 0; i < c.size(); ++i) {
			c.at(i).deconnect(c);
		}
		this._isFuzzy = false;
		for (let i = 0, n = this._cons.length; i < n; ++i) {
			if (this._cons[i].isFuzzy()) {
				this._isFuzzy = true;
				break;
			}
		}
	}

	/**
	 * Changes the status of all variables to unassigned.
	 */
	clearAllVariables() {
		for (const v of this._vars) {
			v.clear();
		}
	}

	/**
	 * Reverse the order of variables.
	 * The index of each variable is reassigned.
	 */
	reverseVariables() {
		this._vars.reverse();
		for (let i = 0, n = this._vars.length; i < n; ++i) {
			this._vars[i].setIndex(i);
		}
	}

	/**
	 * Sorts variables using a specified comparator.
	 * The index of each variable is reassigned.
	 * @param comparator A comparator.
	 */
	sortVariables(comparator) {
		this._vars.sort(comparator);
		for (let i = 0, n = this._vars.length; i < n; ++i) {
			this._vars[i].setIndex(i);
		}
	}

	// Methods for Variables --------

	/**
	 * Returns the number of variables in the problem.
	 * @return Number of variables
	 */
	variableSize() {
		return this._vars.length;
	}

	/**
	 * Returns a variable by index.
	 * @param index Index (0 <= index < getVariableSize()).
	 * @return A variable
	 */
	variableAt(index) {
		return this._vars[index];
	}

	/**
	 * Returns a variable by name.
	 * @param name Name.
	 * @return A variable.
	 */
	variableOf(name) {
		for (const v of this._vars) {
			if (v.name() === name) return v;
		}
		return null;
	}

	/**
	 * Returns whether the variable is contained or not.
	 * @param v A variable.
	 * @return True if contained.
	 */
	hasVariable(v) {
		return this._vars.includes(v);
	}

	/**
	 * Returns the list of variables.
	 * The returned list is not allowed to be modified.
	 * @return The variable list.
	 */
	variables() {
		return this._vars;
	}

	// Methods for Constraints --------

	/**
	 * Gets the number of constraints in the problem.
	 * @return Number of constraints
	 */
	constraintSize() {
		return this._cons.length;
	}

	/**
	 * Returns a constraint with an index.
	 * @param index Index (0 <= index < constraintSize()).
	 * @return A constraint.
	 */
	constraintAt(index) {
		return this._cons[index];
	}

	/**
	 * Returns a constraint by name.
	 * @param name Name.
	 * @return A constraint.
	 */
	constraintOf(name) {
		for (const c of this._cons) {
			if (c.name() === name) return c;
		}
		return null;
	}

	/**
	 * Returns whether the constraint is contained or not.
	 * @param c A constraint
	 * @return True if contained.
	 */
	hasConstraint(c) {
		return this._cons.includes(c);
	}

	/**
	 * Returns the list of constraint.
	 * The returned list is not allowed to be modified.
	 * @return The constraint list.
	 */
	constraints() {
		return this._cons;
	}

	/**
	 * Gets the constraints that exist between the specified variables.
	 * Returns an empty array if no constraints exist.
	 * If there are multiple constraints between two variables (including the case of n-ary constraints (2 < n)), they will be included in the return array.
	 * @param v1 Variable 1
	 * @param v2 Variable 2
	 * @param dest Array of returned values.
	 */
	constraintsBetween(v1, v2, dest) {
		dest.length = 0;
		for (let i = 0, n = v1.size(); i < n; ++i) {
			const c = v1.at(i);
			if (c.constrains(v2)) dest.push(c);
		}
	}

	/**
	 * Finds the set of worst satisfiable constraints in a fuzzy constraint satisfaction problem.
	 * @param dest  Array of returned values.
	 * @return Worst satisfaction degree.
	 */
	constraintsWithWorstSatisfactionDegree(dest) {
		dest.length = 0;
		let cs = 1;
		for (const c of this._cons) {
			const s = c.satisfactionDegree();
			if (s < cs) {
				cs = s;
				dest.length = 0;
				dest.push(c);
			} else if (s - cs < Number.MIN_VALUE * 10) {
				dest.push(c);
			}
		}
		return cs;
	}

	// State acquisition methods --------

	/**
	 * Returns the worst satisfaction degree for the constraints contained in the fuzzy constraint satisfaction problem.
	 * If the degree cannot be determined because the variable has not yet been assigned a value or for some other reason, -1 is returned.
	 * @return Worst satisfaction degree.
	 */
	worstSatisfactionDegree() {
		let cs = 1;
		for (const c of this._cons) {
			const s = c.satisfactionDegree();
			if (s === Constraint.UNDEFINED) return Constraint.UNDEFINED;
			if (s < cs) cs = s;
		}
		return cs;
	}

	/**
	 * Gets the average of satisfaction degrees of the fuzzy constraints.
	 * @return Average of satisfaction degrees.
	 */
	averageSatisfactionDegree() {
		let ave = 0;
		for (const c of this._cons) {
			ave += c.satisfactionDegree();
		}
		ave = ave / this._cons.length;
		return ave;
	}

	/**
	 * Returns the number of variables in the problem that have not been assigned a value.
	 * @return Number of variables with no value assigned.
	 */
	emptyVariableSize() {
		let num = 0;
		for (const v of this._vars) {
			if (v.isEmpty()) num++;
		}
		return num;
	}

	/**
	 * Gets the constraint density (number of constraints/number of variables).
	 * @return Constraint density.
	 */
	constraintDensity() {
		return this.constraintSize() / this.variableSize();
	}

	/**
	 * Returns whether the constraint satisfaction problem has any variables with empty domain.
	 * @return True if it exists.
	 */
	hasEmptyDomain() {
		for (const v of this._vars) {
			if (v.domain().size() === 0) return true;
		}
		return false;
	}

	/**
	 * Returns whether the problem is a fuzzy constraint satisfaction problem, i.e., whether it contains fuzzy constraints.
	 * @return True if it is a fuzzy constraint satisfaction problem.
	 */
	isFuzzy() {
		return this._isFuzzy;
	}

}

/**
 * The class represents a crisp constraint satisfaction problem.
 *
 * @author Takuto Yanagida
 * @version 2023-03-25
 */

class CrispProblem extends Problem {

	/**
	 * Generates a crisp constraint.
	 * @param Array args {
	 *   @type string   'name'      Display name.
	 *   @type Array    'variables' Variables.
	 *   @type Relation 'relation'  A relation.
	 * }
	 * @return A constraint.
	 */
	createConstraint(args) {
		if(args.relation instanceof FuzzyRelation) throw new Error();
		return super.createConstraint(args);
	}

	/**
	 * Returns whether the problem is a fuzzy constraint satisfaction problem, i.e., whether it contains fuzzy constraints.
	 * @return Always returns false.
	 */
	isFuzzy() {
		return false;
	}

	/**
	 * Returns the rate of constraints that are satisfied out of all constraints.
	 * @return Rate of satisfied constraints.
	 */
	satisfiedConstraintRate() {
		return this.satisfiedConstraintSize() / this._cons.length;
	}

	/**
	 * Returns the number of satisfied constraints.
	 * Undefined constraints are ignored.
	 * @return Number of satisfied constraints.
	 */
	satisfiedConstraintSize() {
		let count = 0;
		for (let i = 0; i < this._cons.length; ++i) {
			if (this._cons[i].isSatisfied() === 1) ++count;
		}
		return count;
	}

	/**
	 * Returns a list of violating constraints.
	 * Undefined constraints are ignored.
	 * @param cs Array to get constraints
	 * @return Array of constraints.
	 */
	violatingConstraints(cs) {
		cs.length = 0;
		for (const c of this._cons) {
			if (c.isSatisfied() === 0) cs.push(c);
		}
		return cs;
	}

	/**
	 * Returns the number of violating constraints.
	 * Undefined constraints are ignored.
	 * @return Number of violating constraints.
	 */
	violatingConstraintSize() {
		let count = 0;
		for (const c of this._cons) {
			if (c.isSatisfied() === 0) ++count;
		}
		return count;
	}

}

